body,
html {
  height: 100%;
}

.container {
  display: flex;
  width: 100vw;
  justify-content: center;
  height: 100%;
}

.topBar {
  height: 50px;
  width: 100vw;
  display: flex;
  align-items: center;
  padding: 0 20px;
  background-color: var(--color-1);
  gap: clamp(10px, 2vw, 40px);

  position: fixed;
  top: 0;
}

.topBar > div {
  display: flex;
}

.topBar img {
  width: 30px;
}

.topBar .menu-icon {
  display: none;
}

.menu-icon img:last-of-type {
  display: none;
}

.menu-icon img {
  cursor: pointer;
}

.logo img {
  cursor: pointer;
  width: 120px;
}

/* menu  */
.menu {
  position: fixed;
  min-width: 250px;
  width: auto;
  height: 100%;
  top: 50px;
  left: 0;
  display: flex;
  flex-direction: column;
  gap: 30px;
  background-color: var(--color-2);
  padding: 50px 20px;
  font-family: 'raleway';
  font-weight: 400;
  font-variation-settings: 'wght' 400;
  color: var(--color-4);
  z-index: 999;
  transition: transform 0.3s ease-in-out;
}

.menu img {
  width: 20px;
}

.main-filter {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.main-filter > div > div {
  display: flex;
  gap: 10px;
}

.today,
.upcoming,
.inbox,
.finished,
.notes,
.due {
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 10px;
  padding: 0 5px;
}

.today > p,
.upcoming > p,
.inbox > p,
.finished > p,
.notes > p,
.project > p,
.due > p {
  font-family: 'karla';
  font-size: 1.3rem;
}

.today:hover,
.upcoming:hover,
.inbox:hover,
.due:hover,
.finished:hover,
.notes:hover,
.today.active,
.upcoming.active,
.inbox.active,
.due.active,
.finished.active,
.notes.active {
  background-color: var(--bg-color);
  cursor: pointer;
  /* box-shadow: var(--shadow-elevation-high); */
}

.projects {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.projects > .heading {
  display: flex;
  justify-content: space-between;
}

.heading button {
  cursor: pointer;
  border: none;
  border-radius: 4px;
}

.project-list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-height: 40vh;
  overflow-y: auto;
  overflow-x: hidden;
  scrollbar-width: thin;
  padding-right: 15px;
  box-sizing: content-box;
}

.project-list::-webkit-scrollbar {
  width: 5px;
}

.project-list::-webkit-scrollbar-thumb {
  background: var(--color-1);
}

.project-list > .project {
  display: grid;

  grid-template-areas:
    'color title count'
    '. edit delete';
  grid-template-columns: 15px 1fr auto;
  column-gap: 10px;
  padding: 2px 5px 2px 15px;
  border-radius: 10px;
  align-items: center;
}

.project-list .project:hover,
.project-list .project.active {
  background-color: var(--bg-color);
  cursor: pointer;
}

.project input,
#project-color {
  -webkit-appearance: none;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: none;
  outline: none;
  padding: 0;
  cursor: pointer;
  /* width: 100%; */
}

.project input::-webkit-color-swatch-wrapper,
#project-color::-webkit-color-swatch-wrapper {
  padding: 0;
}

.project input::-webkit-color-swatch,
#project-color::-webkit-color-swatch {
  border: none;
  border-radius: 50%;
}

.project p:nth-of-type(2) {
  justify-self: end;
}

.project button {
  display: none;
  justify-self: end;
  margin-top: 5px;
  border: none;
  border-radius: 5px;
  background-color: transparent;
  cursor: pointer;
}

.project.active button {
  display: block;
}

.project-form-container {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

#project-form,
#projectUpdateForm {
  flex-direction: column;
  gap: 10px;
  padding: 10px 5px 0px 5px;
  border-radius: 10px;
  background: #fff;
  border: 1px solid var(--color-1);
  border-bottom: 4px solid var(--color-1);
}

#project-form {
  display: none;
}

#project-title {
  width: 100%;
  border: none;
  outline: none;
  border-radius: 5px;
  padding: 3px 10px;
  box-shadow: var(--shadow-elevation-low);

  background-color: var(--bg-color);
}

#project-form div:last-of-type,
#projectUpdateForm div:last-of-type {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 3px 0 3px 10px;
  border-radius: 5px;
}
.project-form-btns {
  display: flex;
  gap: 15px;
}

.project-form-btns button {
  cursor: pointer;
  border: none;
  border-radius: 6px;
  padding: 2px 10px;
  background: none;
}

.project-form-btns button:hover {
  background-color: var(--bg-color);
}

/* content  */
.contentBox {
  height: calc((100vh - 50px));
  padding: 20px 50px;

  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  margin-top: 50px;
  font-family: 'karla';
  display: flex;
  justify-content: center;
}

.wrapper {
  max-width: 800px;
  display: flex;
  flex-direction: column;
  gap: 20px;
  min-width: 400px;
}
.contentHead {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.header {
  display: grid;
  grid-template-columns: max-content 1fr;
  gap: 50px;
}

.header h1 {
  font-family: 'raleway';
  color: var(--color-4);
}

.searchBar {
  position: relative;
  display: flex;
  align-items: center;
}

.searchBar img {
  position: absolute;
  left: 5px;
  width: 30px;
}

.searchBar input {
  width: 100%;
  border: none;
  border: 1px solid lightgrey;
  outline: none;
  border-radius: 20px;
  padding: 5px 5px 5px 40px;
  box-shadow: var(--shadow-elevation-low);
  background: transparent;
}

.searchBar input:hover,
.searchBar input:active {
  box-shadow: var(--shadow-elevation-medium);
}

.filter {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: hsl(0deg, 0%, 92%);
  border-radius: 10px;
  padding: 4px 10px;
}

.view {
  display: flex;
  gap: 20px;
}

.view button {
  width: 60px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: transparent;
  border-top: 2px solid transparent;
  border-bottom: 2px solid transparent;
}

.view button:hover {
  border-bottom: 2px solid var(--color-3);
}

.sort {
  display: flex;
  align-items: center;
  gap: 5px;
}

.sort label img {
  width: 35px;
}

.sort select {
  border: none;
  outline: none;
  padding: 5px;
  border-radius: 5px;
  background-color: var(--color-4);
  color: white;
  cursor: pointer;
}

.content {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

.tasks {
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;
}

.task,
.task-form {
  display: grid;
  grid-template-rows: 30px 1fr auto;
  border-radius: 20px;
  gap: 15px;
  padding: 10px;
  box-shadow: var(--shadow-elevation-low);
}

.task-form {
  background-color: var(--bg-color);
}

.taskHeading {
  display: grid;
  grid-template-columns: 20px 1fr 120px;
  align-items: center;
  gap: 20px;
  justify-content: space-between;
}

.taskHeading input[type='checkbox'] {
  appearance: none;
  position: relative;
  width: 1.2rem;
  height: 1.2rem;
  background-color: var(--color-4);
  border-radius: 4px;
  transition: all 0.3s cubic-bezier(0.18, 1.85, 1, 1.07);
  will-change: transform;
  cursor: pointer;
}

.taskHeading input[type='checkbox']::before {
  content: '✔';
  position: absolute;
  font-size: 1.4rem;
  top: -6px;
  left: 1px;
  color: white;
  visibility: hidden;
  transform: scale(0);
}

.taskHeading input[type='checkbox']:checked {
  transform: scale(1.2) translateY(-2px);
}
.taskHeading input[type='checkbox']:checked::before,
.taskHeading input[type='checkbox']:hover::before {
  visibility: visible;
  transition: all 0.3s cubic-bezier(0.18, 1.85, 1, 1.07);
  transform: scale(1);
}

.task-form #task-title,
.task-form #task-label {
  border: none;
  outline: none;
  border-radius: 5px;
  padding: 3px 10px;
  box-shadow: var(--shadow-elevation-low);
  border-top: 2px solid var(--color-4);
  width: 100%;
}

.task-form #task-label {
  padding: 2px 5px;
}

.task .taskHeading .update,
.task-form .taskHeading .update {
  display: flex;
  gap: 10px;
  justify-content: flex-end;
  margin-left: 0;
}

.task .update button,
.task-form .update button {
  border: none;
  border-radius: 5px;
  background-color: #fff;
  cursor: pointer;
}

.task .update img {
  width: 25px;
}

.task-form .update button {
  width: 60px;
  padding: 3px 5px;
  border-top: 2px solid var(--color-4);
  box-shadow: var(--shadow-elevation-low);
}

.task-form #task-description {
  resize: none;
  border: none;
  outline: none;
  border-radius: 5px;
  padding: 5px 10px;
  height: auto;
}

.taskFilter {
  display: flex;
  column-gap: 20px;
  row-gap: 10px;
  align-items: center;
  flex-wrap: wrap;
  justify-content: space-around;
}

.taskFilter div {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 5px;
  flex-grow: 1;
}

.taskFilter div:first-of-type {
  display: flex;
  gap: 5px;
  align-items: center;
}

.color-indicator {
  display: inline-block;
  width: 15px;
  height: 15px;
  border-radius: 50%;
}

.taskFilter div img {
  width: 20px;
}

.task-form .taskFilter div select {
  border: none;
  outline: none;
  border-radius: 5px;
  cursor: pointer;
  background-color: white;
  padding: 2px 5px;
  box-shadow: var(--shadow-elevation-low);
  border-top: 2px solid var(--color-4);
  width: 100%;
}

.task-form input[type='date'] {
  font-size: 16px;
  padding: 2px 5px;
  border: none;
  border-radius: 4px;
  background-color: #fff;
  box-shadow: var(--shadow-elevation-low);
  border-top: 2px solid var(--color-4);
  width: 100%;
}

.task-form input[type='date']:focus {
  outline: 0;
}

.addTask,
.addProject {
  display: flex;
  align-items: center;
  cursor: pointer;
  border: none;
  border-radius: 10px;
  padding: 2px 2px 2px 4px;
}
.addProject {
  padding-left: 8px;
}

.addTask img,
.addProject img {
  width: 30px;
}

.addTask:hover,
.addProject:hover {
  box-shadow: var(--shadow-elevation-medium);
}

/* media query*/

@media (max-width: 768px) {
  .menu {
    width: 0;
    transition: transform 0.3s ease-in-out;
    transform: translateX(-100%);
  }

  .topBar .menu-icon {
    display: block;
  }

  .menu.open {
    min-width: 250px;
    width: auto;
    transform: translateX(0);
  }

  .menu-icon.open img:first-of-type {
    display: none;
  }

  .menu-icon.open img:last-of-type {
    display: block;
  }
}
