.project-list .project.active {\n  background-color: var(--bg-color);\n  cursor: pointer;\n}\n\n.project input,\n#project-color {\n  -webkit-appearance: none;\n  width: 15px;\n  height: 15px;\n  border-radius: 50%;\n  border: none;\n  outline: none;\n  padding: 0;\n  cursor: pointer;\n  /* width: 100%; */\n}\n\n.project input::-webkit-color-swatch-wrapper,\n#project-color::-webkit-color-swatch-wrapper {\n  padding: 0;\n}\n\n.project input::-webkit-color-swatch,\n#project-color::-webkit-color-swatch {\n  border: none;\n  border-radius: 50%;\n}\n\n.project p:nth-of-type(2) {\n  justify-self: end;\n}\n\n.project button {\n  display: none;\n  justify-self: end;\n  margin-top: 5px;\n  border: none;\n  border-radius: 5px;\n  background-color: transparent;\n  cursor: pointer;\n}\n\n.project.active button {\n  display: block;\n}\n\n.project-form-container {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n}\n\n#project-form,\n#projectUpdateForm {\n  flex-direction: column;\n  gap: 10px;\n  padding: 10px 5px 0px 5px;\n  border-radius: 10px;\n  background: #fff;\n  border: 1px solid var(--color-1);\n  border-bottom: 4px solid var(--color-1);\n}\n\n#project-form {\n  display: none;\n}\n\n#project-title {\n  width: 100%;\n  border: none;\n  outline: none;\n  border-radius: 5px;\n  padding: 3px 10px;\n  box-shadow: var(--shadow-elevation-low);\n\n  background-color: var(--bg-color);\n}\n\n#project-form div:last-of-type,\n#projectUpdateForm div:last-of-type {\n  display: flex;\n  justify-content: space-between;\n  align-items: center;\n  padding: 3px 0 3px 10px;\n  border-radius: 5px;\n}\n.project-form-btns {\n  display: flex;\n  gap: 15px;\n}\n\n.project-form-btns button {\n  cursor: pointer;\n  border: none;\n  border-radius: 6px;\n  padding: 2px 10px;\n  background: none;\n}\n\n.project-form-btns button:hover {\n  background-color: var(--bg-color);\n}\n\n/* content  */\n.contentBox {\n  height: calc((100vh - 50px));\n  padding: 20px 50px;\n\n  width: 100%;\n  max-wi